.mymsg-page{
    text-align: center;
    h3{
        line-height: .8rem;
        font-size: .26rem;
        background: skyblue;
        color: white;
        text-align: center;
        .icon-shouye{
            float: left;
            padding-left:.2rem;
             
        }
    }
    p{
        line-height: .5rem;
        margin: .1rem 0;
        span{
            display: inline-block;
            width: 30%;
            text-align: center
        }
        input{
            height: .4rem;
            border:0;
            width: 60%;
            border-bottom: .01rem solid rgb(247, 243, 243);

        }
    }
    .sex-box{
        font-size: .20rem;
        #sexnan,#sexnv{
            width: .15rem ;
            height: .15rem ;
            margin:0 .3rem 0 .2rem; 
        }
        text-align: center;
    }
    .tijiao{
        font-size: .22rem;
        display: inline-block;
        line-height: .6rem;
        width: 80%;
        text-align: center;
        background: rgb(171, 201, 212);

    }
}