.shopingcar-body{
    overflow: hidden;
    flex: 1;
    .car-top{
        font-size: .2rem;
        text-align: center;
        line-height: .8rem;
        margin-top:.2rem; 
        border-bottom: 0.01rem solid rgb(233, 230, 230);
        span{
            font-size:.16rem;
            width: .8rem;
            text-align: center;
        }
    }
    .content{
        min-height: 1.9rem;
        max-height: 4.9rem;
        overflow: auto;
        .text{
            font-size: .16rem;
            color: gray;
            text-align: center;
            line-height: 3rem;
        }
        .content-top{
            height: .4rem;
            display: flex;
            border-bottom: 0.01rem solid rgb(233, 230, 230);
            
            .zuo{
                width: 50%;
                height: .4rem;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding-left:.2rem;

                span{
                    i{
                        width: .2rem;
                        display: inline-block;
                        font-size: .16rem;
                        text-align: center;
                    }
                    font-size: .14rem;
                    padding: 0 .15rem;
                }
            }
            .you{
               line-height: .4rem;
                width: 50%;
                height: .4rem;
                padding-right:.15rem; 
                span{
                    font-size: .14rem;
                    color: red;
                    float: right;
                }
            }
        }
    }
    .pay-box{
        height: .5rem;
        display: flex;
        align-items: center;
        position: fixed;
        width: 100%;
        background: white;
        border-top: 0.01rem solid rgb(238, 235, 235);
        bottom: .5rem;
        z-index: 2;
        label{
            font-size: .16rem;
            width: 40%;
            padding-left:.15rem; 
        }
        span{
            width: 30%;
            height: 100%;
            line-height: .5rem;
            text-align: center;
            font-size: .16rem;
            i{
                color:red;
                font-size: .16rem;
            }
            .total-price{
                font-size: .16rem;
            }
        }
        .pay{
            font-size: .20rem;
            background: red;
            color: white;
            
        }
    }
    .main{
        height: 1.5rem;
        display: flex;
        
        
        .box{
            width: 10%;
            text-align: center;
        }
        .good-msg{
           width: 70%;
           display: flex;
           img{
               width: 30%;
               padding:.3rem 0; 
           }
           .title{
               width: 70%;
               padding: .2rem .1rem;
               display: inline-block;
               display: flex;
               flex-direction: column;
               justify-content: space-around;
               font-size: .12rem;
               i{
                   font-size: .12rem;
               }
               span{
                   color:red;
                   .oprice{
                       color:gray;
                       text-decoration: line-through;
                   }
               }
               .size{
                   color:black;
                   font-size: .14rem;
               }
           }
        }
        .buynum{
            .del{
                display: block;
                height: .3rem;
                text-align: center;
                line-height: .3rem; 
                margin-top:.2rem;
                font-size: .1rem; 
            }
            width: 20%;
            text-align: center;
            line-height: 1rem;

        }
    }
}