.type.body{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.type-main{
    flex: 1;
    overflow: auto;
    background: white;
    width: 100%;
    height: 100%;
    padding-top:.2rem; 
    .text{
        font-size: .16rem;
        color: gray;
    }

    .type-box,.active{
        width: 25%;
        line-height: .5rem;
        height: .5rem;
        background: rgb(247, 245, 245);
        border-bottom: .01rem solid rgba(128, 128, 128, 0.096);
        span{
            float: left;
            
            width: 100%;
            height: 100%;
            font-size: .16rem;
            line-height: .5rem;
            padding-left:.12rem; 
            box-sizing: border-box;
        }
        
    }
    .active{
        background: rgb(245, 227, 227);
    }
    .goodslist{
        width: 72%;
        position: absolute;
        top:.7rem;
        padding-left:3%; 
        right: 0;
        line-height: 1rem;
        a{
            width: 30%;
            height: 1rem;
            padding: 1%;
            display: inline-block;
            text-align: center;
            color: black;
            p{
                font-size: .12rem !important;
                line-height: .2rem;
            }
        }
    }

}

p{
    font-size: .16rem !important;
}