.register{
    width: 100%;
    height: 100%;
    font-size: .16rem;
    .logo{
        width: 100%;
        height: 2.2rem;
        line-height: 2.2rem;
        text-align: center;
        padding-top: .8rem;
        img{
            width: 1rem;
            height: 1rem;
            vertical-align: middle;
            display: inline-block;
            border-radius: .1rem;
        }
    }
    p{
        display: flex;
        flex-direction: column;
        height: 1rem;
        align-items: center;
        span,em{
            text-align: center;
            font-style: normal;
            line-height: .5rem;
        }
        span{
            color:white;
            background: red;
            width: 80%;
            font-size: .16rem;
            border-radius: .3rem;
            
        }
        em{
            color:gray;
            font-size: .12rem;
        }
        .tel{
            width: 80%;
            line-height: .5rem;
            text-align: center;
            border-radius: .3rem;
            font-size: .16rem;
            background: white;
            border:.01rem solid gray;
            color:black;
           margin-top: .2rem;
            .icon-shouji{
                color: red;
            }
        }
    }
    .user,.pwd{
        display: flex;
        justify-content: center;
        flex-direction: row;
        height: .4rem;;
        line-height:.4rem;
        margin:.2rem 0; 
        i{
            width: 15%;
            text-align: center;
        }
        input{
            width: 60%;
            border:none;
            height: .4rem;
            border-bottom: 0.01rem solid gray;
            margin-right:.3rem; 
        }
    }
    .dlbtn{
        height: .5rem;
    }
    .xy{
        color:gray;
        padding-top:.3rem; 
        height: .3rem;
    }
    .icon-xiangzuojiantou{
        position: fixed;
        left: .2rem;
        top: 0.4rem;
    }
}
