.goods-list{
    li{
        width: 48%;
        // box-sizing: border-box;
        padding:1%;
        font-size: .16rem;
        // border: 0.01rem solid #ccc;
        box-shadow: .02rem .02rem .02rem rgba(153, 153, 153, 0.342);
        
        a{
            padding:.1rem;
            display: block;
            
            .img-box{
                height: 50%;
                overflow-y: hidden;
                img{
                    width: 100%;
                    height: auto;
                }
            }

            h3{
                $h:.28rem;
               
                color:#333;
                height: 1.5*$h;
                line-height: 1.5*$h;
                overflow-y: hidden;
                font-weight: normal;
                font-size: .14rem;
            }

            .price{
                $h:.3rem;
                color:#999;
                font-size:.12rem !important;
                line-height: $h;
                .frt{
                    font-size: .12rem;
                    del{
                        font-size: .12rem;
                    }
                }
                span{
                    color:#E20;
                    font-size:.16rem;
                }
            }
        }
    }
}