
@import "./font_920912_2j2ddpqu173/iconfont.css";

html,body,div,a,input,img,p,h1,h2,h3,h4,h5,h6,ul,li,i,p,span,dl,td,tr,table,dt,dd,button{
    margin: 0;
    padding: 0;
}
html,body{
	width: 100%;
    height: 100%;
    font-size: -webkit-calc((100vw/7.5)*2);
    background: white;

}
.ellipsis2{
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}
i{
    font-style: normal;
}
ul,
li{
    list-style-type: none;
}
a{
    text-decoration: none;
}
img{
    display: block;
    width: 100%;
    height: auto;
}

.flt{
    float : left;
}
.frt{
    float: right;
}
.clr:after{
    content: "";
    display: block;
    clear: both;
}
.btn{
    display: block;
    border-radius: .05rem;
    text-align: center;
}

.iconfont{
    font-size: .24rem;
}
#root{
    height: 100%;
    width: 100%;
}
.page-box{
    height: 100%;
}
.router-box{
    height: 100%;
}

//底部
.foot-box{
    height: .5rem;
    z-index: 3;
}
footer{
    height: .5rem;
    border-top:0.01rem solid rgb(241, 238, 238);
    display: flex;
    background: white;
    position: fixed;
    bottom: 0;
    width: 100%;
    .nav-a{           
        // display: inline-block;
        width: 25%;;
        text-align: center;
        line-height: .20rem;
        color:gray;;
        height: 100%;          
        font-size: .12rem;
        padding: .05rem 0;
        i{  
            display: inline-block;
            font-size: .22rem;
            width: 100%;
            
        }
        
    }
    .active{
        color: rgb(255, 0, 0);
        
    }
    
}

  