.tel-register{
    font-size: .16rem;
    background: white;
    .head{
        padding-top:.3rem; 
        height: .8rem;
        border-bottom: .01rem solid gray;
        .icon-xiangzuojiantou{
            position: absolute;
            left: .2rem;
            top: .58rem;
        }
        .title{
            line-height: .8rem;
            padding-right:.2rem; 
            font-size: .18rem;
           
        }
    }
    .tel-content{
        padding: .2rem;
        height: 4.5rem;
        h3{
            margin:.3rem 0; 
            font-size: .24rem;
            p{
                font-size: .14rem;
                line-height: .3rem;
                color:gray;
                font-weight: 100;
                border: none;
            }
        }
        p{
            line-height: .6rem;
            border-bottom: .01rem solid rgb(223, 217, 217);
            margin-top:.2rem; 
            input{
                height: .4rem;
                border:none;
               
            }
            .phone{
                width: 100%;
            }
            .yzm{
                width: 70%;
                
            }
            button{
                border: none;
                background: white;  
            }
        }
        .next{
            margin-top:.4rem; 
            display: inline-block;
            width: 100%;
            height: .5rem;
            background: rgb(238, 237, 237);
            font-size: .18rem;
            color:white;
            text-align: center;
            line-height: .5rem;
        }
    }
    .xy{
        
        bottom: .4rem;
        text-align: center;
    }
}


