.My-page{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex:1;
    .shezhi-box{
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgba(39, 38, 38,.2);
        z-index: 30;
        .shezhi{
            position: absolute;
            right: .5rem;
            top:.6rem;
            background: rgb(231, 225, 225);
            border-radius: .1rem;
            p{
                line-height: .36rem;
                padding: 0 .2rem;
            }
        }
    }
    .my-top{
        height: .5rem;
        width: 100%;
        background: red;
        padding-top: .2rem;
        line-height: .5rem;
        h3{
            color:white; 
            text-align: center;
            font-size: .22rem;
            .icon-xiaoxi{
                float: left;
                margin-left:.2rem; 
            }
            .icon-shezhi1{
                float: right;
                margin-right:.2rem; 
            }
        }
       
    }
    .my-main{
        .pic-box{
            width: 100%;
            background: red;
            padding: .15rem 0;
            .pic{
                width: .8rem;
                height: .8rem;
                border-radius: 50%;
                background: url("http://placehold.it/80x80");
                margin-left:.2rem;  
                display: inline-block;
            }
            span{
                font-size: .16rem;
                display: inline-block;
                height: 100%;
                color:white;
            }
        }
        .login{
            font-size: .18rem;
            line-height: .8rem;
            position: relative;
            a{
                display: inline-block;
                width: 50%;
                text-align: center;
                color:white;
                box-sizing: border-box;
                height: .4rem;   
                vertical-align: middle;
                line-height: .4rem;
            }
            .lg{
                border-right: .01rem solid white;
            }
            .hot{
                position: absolute;
                right: .2rem;
                top:.15rem;
                width: .5rem;
                height: .18rem;
                padding: 0;
                .am-badge-text{
                    background: yellow;
                    color:red;
                }
            }
        }
        .order{
            margin: 0 .1rem;
            
            p{
                font-size: .16rem;
                line-height: .4rem;
                border-bottom: .01rem solid rgb(231, 224, 224);
                i{
                    float: right;
                    color: gray;
                }
            }
            .order-box{
               
                display: flex;
                justify-content: space-around;
                span{
                    font-size: .14rem;
                    display: flex;
                    line-height: .32rem;
                    flex-direction: column;
                    text-align: center;

                }
            }
            
        }
        .my-money{
            padding:0 .1rem;
            border-top:.12rem solid rgb(241, 237, 237); 
            border-bottom:.12rem solid rgb(241, 237, 237);
            p{
                font-size: .16rem;
                line-height: .4rem;
                border-bottom: .01rem solid rgb(231, 224, 224);
            }
        }
        .money-box{
            margin:0 .1rem; 
            display: flex;
            justify-content: space-around;
            span{
                font-size: .14rem;
                display: flex;
                line-height: .32rem;
                flex-direction: column;
                text-align: center;
                color:gray;
                i{
                    color:black;
                }

            }
        }
        .list{
            border-bottom: .12rem solid rgb(231, 224, 224);
            p{
                font-size: .14rem;
                line-height: .4rem;
                padding: 0 .2rem;
                span{
                    float: right;
                    color:gray;
                    i{
                        float: right;
                        font-size: .14rem;
                        color:gray;
                    }
                }
            }
        }
        
    }
}