
//home页面
.home-body{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    .post-div{
        height: 1.1rem;
        width: 100%;
    }
    .top-box{
        position: fixed;
        overflow: hidden;
        left: 0;
        top: 0;
        z-index: 2;
        width: 100%;
        //顶部
        .home-top{
            width: 100%;
            .am-navbar{
                padding-top:.25rem; 
                background: red;
                .am-navbar-left{
                   box-sizing: border-box;
                   width: 10%;
                    padding: .2rem;
                }
                 //搜索框
                .search{
                    height: .3rem;
                    border: 0;
                    display: inline-block;
                    width: 2.2rem;
                    border:none;
                    border-radius:.1rem; 
                    padding-left:.2rem; 
                    box-sizing: border-box;
                    margin-right:.2rem; 
                }
            }
        }

       

        //商品导航
        .home-box{
            width: 100%;
            display: flex;
            height: .4rem;
            background: white;
            .goodType{
                width: 85%;
                line-height: .4rem;
                overflow: auto;
                .type-box{
                    width: 200%;
                    height: 100%;
                    display: flex;
                    white-space:nowrap; 
                    a{
                        display: block;
                        font-size: .16rem;
                        width: 14%;
                        height: 100%;
                        color: black;
                        text-align: center;
                    }
                } 
            }
            .home-menu{
                width: 15%;
                height: 100%;
                line-height: .4rem;
                text-align: center;
                font-size: 16px;
            }
        }
    }
}





  