.goodshow{
    display: flex;
    flex-direction: column;
    position: relative;
    .icon-xiangzuojiantou{
        position: absolute;
        left: .2rem;
        top:.2rem;
        background: rgba(80, 78, 78,.3);
        color:white;
        width: .3rem;
        height: .3rem;
        border-radius:50%;
        text-align: center;
        line-height: .3rem; 
    }
    .box{
        height: .4rem;
        line-height: .4rem;
        display: flex;
        align-items: center;
        span{
            font-size: .14rem;
            padding: 0  0.05rem;
            line-height: .4rem;
            i{
                font-size: .18rem;
                color:red;
                line-height: .4rem;
            }
        }
        .email{
            background: red;
            color:white;
            height: .2rem;
            font-size: .1rem;
            line-height: .2rem;
            
        }
        .oprice{
            color:gray;
            font-size: .12rem;
            text-decoration: line-through;
            line-height: .4rem;
        }
        .join_number{
            color:gray;
            font-size: .12rem;

            line-height: .4rem;
        }
    }
    .title{
        .goods_title{
            font-size: .16rem; 
            line-height: .2rem;
            position: relative;
            margin: .1rem;
            padding-bottom:.12rem;
            border-bottom: .01rem solid rgb(207, 200, 200); 
            i{
                position: absolute;
                right: .2rem;
                font-size: .2rem;
                line-height: .2rem;
            }
        }
        .clr{
            padding-left:.1rem; 
            li{
                float: left;
                font-size: .13rem;
                list-style:disc;
                margin:0  .15rem;
                line-height: .3rem;
            }
            .flt{
                float: right;
                list-style: none;
                font-size: .2rem;
            }
        }

    }
    .comment{
        padding: .1rem;
        min-height: .5rem;
        flex: 1;
        h3{
            font-size: .16rem;
        }
    }
    .intogwc{
        height: .5rem;
        display: flex;
        border-top:.01rem solid rgb(219, 217, 217);
        box-sizing: border-box;
        position: fixed;
        bottom: 0;
        width: 100%;
        background: white;
        z-index: 9;
        .kefu, .gwc{
            font-size: .12rem;
            width: 15%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border-right: .01rem  solid rgb(214, 211, 211);
        }
        .buy, .jion{
            font-size: .18rem;
            text-align: center;
            line-height: .5rem;
            
        }
        .buy{
            width: 30%;
            
        }
        .jion{
           
            width: 40%;
            background: red;
            color:white;
            border-top:.01rem solid red;
        }
    }
}
.addcar-box{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 20;
    overflow: auto;
    background: rgba(206, 201, 201,.9);
    .addcar{
        position: absolute;
        bottom: 0;
        height: 4.7rem;
        width: 100%;
        background: white;
        z-index: 1000;
        .smallimg{
            width: 30%;
            height: auto;
            position: absolute;
            top:-.5rem;
            left: .2rem;
        }
        .price{
            text-align: center;
            color: red;
            font-size: .2rem;
            line-height: .3rem;
            span{
                display: block;
                font-size: .14rem;
                padding-left:1.5rem; 
                text-align: left;
                color: gray;
            }
        }
        h3{
            font-size: .18rem;
            font-weight: 100;
            line-height: .5rem; 
            padding-left:.2rem; 
        }
        .color{
            padding-left:.2rem; 
            overflow: auto;
            min-height: .3rem;
            max-height: .9rem;
            display: flex;
            flex-wrap:wrap;
            justify-content: space-around;
            span{
                
                margin:.1rem .05rem; 
                width: 29%;
                height: .3rem;
                background: rgb(221, 218, 218);
                color:white;
                text-align: center;
                line-height: .3rem;
            }
            .active{
                background: red;
            }
        }
        ul{
            width: 100%;
            min-height: .4rem;
            max-height: 1.2rem;
            overflow: auto;
            display: flex;
            flex-wrap:wrap;
            .actived{
                border:.01rem solid red;
                color:red;
                font-weight: bold;
            }
            li{
                display: flex;
                flex-direction: column;
                width: 30%;
                border:.01rem solid rgb(92, 87, 87);
                margin: 1%;
                height: .4rem;
                position: relative;
                
                
                p{
                    line-height: .25rem;
                    white-space:nowrap;
                    overflow: hidden;
                    height: .25rem;
                    text-align: center;
                }
                span{
                    font-size: .12rem;
                    text-align: center;
                    line-height: .15rem;
                    background: rgb(245, 245, 250);
                    position: absolute;
                    bottom: 0;
                    display: inline-block;
                    width: 100%;
                }
            }
        }
        .buynum{
            height: .5rem;
            display: flex;
            align-items: center;
            padding:0 .2rem; 
            justify-content: space-between;
            span{
                font-size: .16rem;
            }
            p{
                display: flex;
                font-size: .16rem;
                height: 100%;
                line-height: .5rem;
                align-items: center;
                input{
                    width: .35rem;
                    height: .2rem;
                    border-top:.01rem solid gray;
                    text-align: center;
                }
               
                i{
                    display: inline-block;
                    width: .2rem;
                    height: .2rem;
                    line-height: .2rem;
                    text-align: center;
                    border:0.01rem solid gray;
                }
            }
        }
        .okbtn{
            width: 100%;
            height: .5rem;
            position: absolute;
            bottom: 0;
            background:red;
            font-size: .24rem;
            color:white;
            text-align: center;
            line-height: .5rem;
        }
        .colsebtn{
            position: absolute;
            right: .1rem;
            top: .1rem;
            width: .3rem;
            height: .3rem;
            font-size: .24rem;
            text-align: center;
            line-height: .3rem;
            color: gray;
        }
    
    }
}
