.main{
    flex: 1;
    overflow: auto;
    // overflow: hidden;
     //轮播图
    .swiper-slide {
        background: #DEF1E5;
        overflow: hidden;
        width: 100% !important;
        img{
            width: 100%;
        }
    }
    .banner{
        .banner-box1{
            width: 100%;
            height: .8rem;
            a{
                display: inline-block;
                width: 25%;
                height: 100%; 
            } 
        }
        .banner-box2{
            margin-top:.2rem; 
            width: 98%;
            padding: 0 1%;
            .miaosha{
                img{
                    width: 50%;
                    float: left;
                }
            }
        }
        
    }
}